import { Storage } from '@ionic/storage';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Media, MediaObject } from '@ionic-native/media';
import { File } from '@ionic-native/file';


@Component({
  selector: 'page-playlist',
  templateUrl: 'playlist.html',
})
export class PlaylistPage {
  playlists: any;
  mediaFile: MediaObject;
  isPlaying: boolean = false;

  constructor(public navCtrl: NavController, public storage: Storage, private media: Media, private file: File) {
    this.storage.get('downloads').then((downloads) => {
      this.playlists = downloads;
      console.log('list', downloads)
    });
  }

  playAudio(audio) {
    this.mediaFile = this.media.create(audio);
    this.mediaFile.play();
    this.isPlaying = true;
  }

  pauseAudio() {
    this.mediaFile.pause();
    this.isPlaying = false;
  }

}



